<template>
  <div id="loading-container" class="vs-con-loading__container">
    <nv-card>
      <vs-table class="mb-base" :data="postingRuleTableData" :noDataText="postingRuleTableNoDataText" :sst="true" search @search="filterPostingRuleTable" @sort="orderPostingRuleTable">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="add" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isFundsTransferAll')">{{ $t('ADD') }}</vs-button>
            <vs-button class="p-3 mr-4" @click="refreshPostingRuleTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="postingRuleTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ postingRuleTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="postingRuleTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="position">#</vs-th>
          <vs-th>
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th>
            {{ this.$t('APP') }}
          </vs-th>
          <vs-th>
            {{ this.$t('TRANSFER_TYPE') }}
          </vs-th>
          <vs-th>
            {{ this.$t('POSTING_TYPE') }}
          </vs-th>
          <vs-th>
            {{ this.$t('CURRENCY_TYPE') }}
          </vs-th>
          <vs-th>
            {{ this.$t('AMOUNT') }}
          </vs-th>
          <vs-th>
            {{ this.$t('TIME') }}
          </vs-th>
          <vs-th>
            {{ this.$t('OPTIONS') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].position">
              {{ data[index].position }}
            </vs-td>
            <vs-td :data="data[index].ruleId">
              <router-link :to="{ name: 'ViewPostingRule', params: { id: data[index].ruleId } }">
                <span :class="disabledClass(data[index])">{{ data[index].ruleId }}</span>
              </router-link>
            </vs-td>
            <vs-td :data="data[index].appName">
              {{ data[index].appName }}
            </vs-td>
            <vs-td :data="data[index].transferTypeName">
              {{ data[index].transferTypeName }}
            </vs-td>
            <vs-td :data="data[index].postingTypeDescription">
              {{ data[index].postingTypeDescription }}
            </vs-td>
            <vs-td :data="data[index].currencyTypeDescription">
              {{ data[index].currencyTypeDescription }}
            </vs-td>
            <vs-td :data="data[index].fromAmount">
              {{ data[index].fromAmount | currency('', 0) | placeholder('*') }} - {{ data[index].toAmount | currency('', 0) | placeholder('*') }}
            </vs-td>
            <vs-td :data="data[index].fromTime">
              {{ data[index].fromTime | placeholder('-') }} - {{ data[index].toTime | placeholder('-')  }}
            </vs-td>
            <vs-td :data="data[index].options">
              <div class="flex">
                <vs-button class="p-1 mr-1" color="primary" @click="moveUp(data[index])" icon-pack="far" icon="fa-arrow-up"></vs-button>
                <vs-button class="p-1 mr-1" color="primary" @click="moveDown(data[index])" icon-pack="far" icon="fa-arrow-down"></vs-button>
              </div>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="postingRuleTableTotalPages" v-model="postingRuleTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import PostingRuleTableMixin from '@/mixins/ft/PostingRuleTableMixin'
import PostingRuleService from '@/services/ft/PostingRuleService'

export default {
  name: 'posting-rule-browse',
  mixins: [AlertMixin, DisabledClassMixin, PostingRuleTableMixin],
  methods: {
    add () {
      this.$router.push({ name: 'CreatePostingRule' })
    },
    moveUp (rule) {
      let self = this
      PostingRuleService.positionPostingRule({
        ruleId: rule.ruleId,
        position: (rule.position - 1)
      }).then(function (response) {
        self.refreshPostingRuleTable()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    moveDown (rule) {
      let self = this
      PostingRuleService.positionPostingRule({
        ruleId: rule.ruleId,
        position: (rule.position + 1)
      }).then(function (response) {
        self.refreshPostingRuleTable()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    }
  },
  mounted: function () {
    this.loadPostingRuleTableData()
  }
}
</script>
