import PostingRuleService from '@/services/ft/PostingRuleService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let PostingRuleTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultPostingRuleTableFilter: {
      type: String,
      default: null
    },
    defaultPostingRuleTableOrderBy: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      postingRuleTableData: [],
      postingRuleTableFilterTimer: null,
      postingRuleTableFilter: this.defaultPostingRuleTableFilter,
      postingRuleTableOrderBy: this.defaultPostingRuleTableOrderBy,
      postingRuleTableContainer: 'loading-container',
      postingRuleTableBusy: false,
      postingRuleTableTotalRows: 0,
      postingRuleTableCurrentPage: 1,
      postingRuleTablePerPage: 10
    }
  },
  computed: {
    postingRuleTableNoDataText: function () {
      return this.postingRuleTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    postingRuleTableTotalPages: function () {
      return Math.ceil(this.postingRuleTableTotalRows / this.postingRuleTablePerPage)
    },
    postingRuleTablePagingInfo: function () {
      return this.pagingInfo(this.postingRuleTableCurrentPage, this.postingRuleTablePerPage, this.postingRuleTableTotalRows)
    }
  },
  methods: {
    loadPostingRuleTableData (loadingContainerId) {
      let self = this
      this.postingRuleTableBusy = true
      this.postingRuleTableContainer = loadingContainerId || this.postingRuleTableContainer
      this.startLoading(this.postingRuleTableContainer)
      PostingRuleService.postingRules({
        pageIndex: this.postingRuleTableCurrentPage - 1,
        pageSize: this.postingRuleTablePerPage,
        filter: this.postingRuleTableFilter,
        orderByField: this.postingRuleTableOrderBy,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.postingRuleTableContainer)
        self.postingRuleTableBusy = false
        self.postingRuleTableTotalRows = response.data.totalRows
        self.postingRuleTableData = response.data.postingRuleList || []
      }).catch(function (error) {
        self.stopLoading(self.postingRuleTableContainer)
        self.postingRuleTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshPostingRuleTable () {
      this.loadPostingRuleTableData()
    },
    filterPostingRuleTable (filter) {
      let self = this
      if (this.postingRuleTableFilter !== filter) {
        clearTimeout(this.postingRuleTableFilterTimer)
        this.postingRuleTableFilterTimer = setTimeout(() => {
          self.postingRuleTableFilter = filter
          self.loadPostingRuleTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.postingRuleTableFilterTimer)
      }
    },
    orderPostingRuleTable (orderBy, active) {
      this.postingRuleTableOrderBy = (active ? '' : '-') + orderBy
      this.loadPostingRuleTableData()
    }
  },
  watch: {
    postingRuleTableCurrentPage: function () {
      this.refreshPostingRuleTable()
    },
    postingRuleTablePerPage: function () {
      this.refreshPostingRuleTable()
    }
  }
}

export default PostingRuleTableMixin
