import Endpoint from '@/services/Endpoint'

export default {
  postingRules (payload) {
    return Endpoint().get('/s1/ft/postingRule', {
      params: payload
    })
  },
  createPostingRule (payload) {
    return Endpoint().post('/s1/ft/postingRule', payload)
  },
  modifyPostingRule (payload) {
    return Endpoint().patch('/s1/ft/postingRule', payload)
  },
  positionPostingRule (payload) {
    return Endpoint().patch(`/s1/ft/postingRule/${payload.ruleId}/position`, payload)
  }
}
